import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import {faUserGroup, faSignInAlt, faInbox, faHome, faUser}  from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
function CollapsibleExample() {
  return (
    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
      <Container>
        <Navbar.Brand href="#home"><img src='https://thumpin.ac.th/uploads/web/20210715101059emZtuhh/logo/2021080416052287eOuOs.png' width='50' height='50'/></Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="#features"><FontAwesomeIcon icon={faHome}/> หน้าแรก</Nav.Link>
            <Nav.Link href="#pricing"><FontAwesomeIcon icon={faInbox}/> สอบถามเพิ่มเติม</Nav.Link>
            <Nav.Link href="#pricing"><FontAwesomeIcon icon={faUserGroup}/> แขกเข้ามาเยือน</Nav.Link>
          </Nav>
          <Nav>
            <Nav.Link href="#deets">                              <FontAwesomeIcon icon={faSignInAlt}/>  เข้าสู่ระบบ</Nav.Link>
            <Nav.Link href="#memes">
            <FontAwesomeIcon icon={faUser}/> สมัครสมาชิก
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default CollapsibleExample;